<template>
    <div class="addDetail-box">
       <Header>
            <van-icon :style="{color:'#fff'}" tag="router-link" to='/address' slot="first" name="arrow-left" info="" />
          <p slot="mid"><b>搜索地址</b></p>
          <p slot="last"></p>
        </Header>
        <!-- 身体 -->
        <section class="addDetail-con">
            <div class="addDetail-con-divOne">
                <van-field v-model="txt" placeholder="请输入小区/写字楼/学校等" class="search" />
                <van-button @click="Send()"
                 type="info" :style="{width:'18%',height: '0.64rem',borderRadius:'0.06rem'}">确认</van-button>
            </div>

            <div data-v-78b5a23a="" class="warnpart">为了满足商家的送餐要求，建议您从列表中选择地址</div>
            <!-- 渲染出来的数据 -->
            <ul class="addDetail-ul">
                <li v-for="(item,k) in list" :key='k' @click="ren(item)">
                    <p>{{item.name}}</p>
                    <p>{{item.address}}</p>
                </li>
            </ul>


            <div class="addDetail-txt">
                <p>找不到地址？</p>
                <p>请尝试输入小区、写字楼或学校名</p>
                <p>详细地址（如门牌号）可稍后输入哦。</p>
            </div>
        </section> 
    </div>
</template>

<script>
import Header from '@/components/header'

export default {
components:{
    Header
},
data() {
    return {
        txt:'',
        list:[]
    }
},
methods: {
    Send(){
         this.axios.get(`http://elm.cangdu.org/v1/pois?type=nearby&keyword=${this.txt}`).then((res)=>{
            console.log(res)
            this.list=res.data
        })
    },
    ren(item){
        //console.log(item)
        this.$router.push({ 
            name:'Address',
            params:{
                Cname:item.name,
                address:item.address,
                geohash:item.geohash,
            }
        })
    }
},
mounted() {
   
},
}
</script>

<style lang="scss">
p{
    margin: 0;padding: 0;
}
.addDetail-box{
    width: 100%;height: 100%;
    background: #f2f2f2;
    display: flex;
    flex-direction: column;
}
.addDetail-con{
    >.addDetail-con-divOne{
        height: 1rem;width: 100%;
        background: #fff;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
}
 .search {
     width:76%;line-height: 0.42rem;
    background: #f2f2f2;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 0.24rem;
 } 

 .warnpart[data-v-78b5a23a] {
    background: #fff6e4;
    font-size: 0.22rem;
    color: #ff883f;
    line-height: 0.45rem;
    text-align: center;
}


.addDetail-txt {
    height: 1.54rem;width:100%;
    >p{  
        text-align:center;
        line-height: 0.34rem;
        font-size: 0.26rem;
        color: #969696;
        margin-bottom: 0.2rem;
        
    }
}

.addDetail-ul{
    height: 0.9rem;
    background: #f2f2f2;
    li{
        height: 0.7rem;
        border-bottom: 0.008rem solid #ccc;
        p{
            padding-left:0.2rem;height: 50%; 
            line-height: 0.35rem;
            font-size: 0.2rem;
        }
    }
}
</style>


